import React from "react";
import { Divider } from 'antd';

import styled from "styled-components";

const Box = styled.div`
    flex-grow: 0;
    border-radius: 4px;
    position: relative;
    padding: 10px;
    box-sizing: border-box;
    height: 30px;
    margin: 4px;
    width: 30px;
    cursor: pointer;
    background-color: #fff;
    animation: 0.3s ease-in-out both fade-in, 0.5s ease-in-out both color;
`;

const Summary = styled.div`
    
`

const DeadlineVisualizer = ({deadline}) => (
    <div>
        <Divider>{ deadline.title }</Divider>
        {deadline.boxes.map((item, index) => <Box key={index} passed={item.passed}/>)}
        <Summary>{deadline.summary}</Summary>
    </div>
);

export default DeadlineVisualizer;